/**
 * 
 * @param {*} note (pack)
 */
const summarize = (note) => {
    if (note.deletedAt) {
        return '()';
    }
    if (note.isHidden) {
        return '()';
    }
    let summary = '';
    // 
    if (note.cw != null) {
        summary += note.cw;
    }
    else {
        summary += note.text ? note.text : '';
    }
    // 
    if ((note.files || []).length != 0) {
        summary += ` (${note.files.length})`;
    }
    // 
    if (note.poll) {
        summary += ' ()';
    }
    // 
    if (note.replyId) {
        if (note.reply) {
            summary += `\n\nRE: ${summarize(note.reply)}`;
        }
        else {
            summary += '\n\nRE: ...';
        }
    }
    // Renote
    if (note.renoteId) {
        if (note.renote) {
            summary += `\n\nRN: ${summarize(note.renote)}`;
        }
        else {
            summary += '\n\nRN: ...';
        }
    }
    return summary.trim();
};
export default summarize;
