import * as getCaretCoordinates from 'textarea-caret';
import { toASCII } from 'punycode';
export default {
    bind(el, binding, vn) {
        const self = el._autoCompleteDirective_ = {};
        self.x = new Autocomplete(el, vn.context, binding.value);
        self.x.attach();
    },
    unbind(el, binding, vn) {
        const self = el._autoCompleteDirective_;
        self.x.detach();
    }
};
/**
 * 
 */
class Autocomplete {
    /**
     * 
     */
    constructor(textarea, vm, opts) {
        //#region BIND
        this.onInput = this.onInput.bind(this);
        this.complete = this.complete.bind(this);
        this.close = this.close.bind(this);
        //#endregion
        this.suggestion = null;
        this.textarea = textarea;
        this.vm = vm;
        this.opts = opts;
        this.opening = false;
    }
    get text() {
        return this.vm[this.opts.model];
    }
    set text(text) {
        this.vm[this.opts.model] = text;
    }
    /**
     * 
     */
    attach() {
        this.textarea.addEventListener('input', this.onInput);
    }
    /**
     * 
     */
    detach() {
        this.textarea.removeEventListener('input', this.onInput);
        this.close();
    }
    /**
     * 
     */
    onInput() {
        const caretPos = this.textarea.selectionStart;
        const text = this.text.substr(0, caretPos).split('\n').pop();
        const mentionIndex = text.lastIndexOf('@');
        const hashtagIndex = text.lastIndexOf('#');
        const emojiIndex = text.lastIndexOf(':');
        const max = Math.max(mentionIndex, hashtagIndex, emojiIndex);
        if (max == -1) {
            this.close();
            return;
        }
        const isMention = mentionIndex != -1;
        const isHashtag = hashtagIndex != -1;
        const isEmoji = emojiIndex != -1;
        let opened = false;
        if (isMention) {
            const username = text.substr(mentionIndex + 1);
            if (username != '' && username.match(/^[a-zA-Z0-9_]+$/)) {
                this.open('user', username);
                opened = true;
            }
        }
        if (isHashtag && !opened) {
            const hashtag = text.substr(hashtagIndex + 1);
            if (!hashtag.includes(' ')) {
                this.open('hashtag', hashtag);
                opened = true;
            }
        }
        if (isEmoji && !opened) {
            const emoji = text.substr(emojiIndex + 1);
            if (!emoji.includes(' ')) {
                this.open('emoji', emoji);
                opened = true;
            }
        }
        if (!opened) {
            this.close();
        }
    }
    /**
     * 
     */
    async open(type, q) {
        if (type != this.currentType) {
            this.close();
        }
        if (this.opening)
            return;
        this.opening = true;
        this.currentType = type;
        //#region 
        const caretPosition = getCaretCoordinates(this.textarea, this.textarea.selectionStart);
        const rect = this.textarea.getBoundingClientRect();
        const x = rect.left + caretPosition.left - this.textarea.scrollLeft;
        const y = rect.top + caretPosition.top - this.textarea.scrollTop;
        //#endregion
        if (this.suggestion) {
            this.suggestion.x = x;
            this.suggestion.y = y;
            this.suggestion.q = q;
            this.opening = false;
        }
        else {
            const MkAutocomplete = await import('../components/autocomplete.vue').then(m => m.default);
            // 
            this.suggestion = new MkAutocomplete({
                parent: this.vm,
                propsData: {
                    textarea: this.textarea,
                    complete: this.complete,
                    close: this.close,
                    type: type,
                    q: q,
                    x,
                    y
                }
            }).$mount();
            // 
            document.body.appendChild(this.suggestion.$el);
            this.opening = false;
        }
    }
    /**
     * 
     */
    close() {
        if (this.suggestion == null)
            return;
        this.suggestion.destroyDom();
        this.suggestion = null;
        this.textarea.focus();
    }
    /**
     * 
     */
    complete(type, value) {
        this.close();
        const caret = this.textarea.selectionStart;
        if (type == 'user') {
            const source = this.text;
            const before = source.substr(0, caret);
            const trimmedBefore = before.substring(0, before.lastIndexOf('@'));
            const after = source.substr(caret);
            const acct = value.host === null ? value.username : `${value.username}@${toASCII(value.host)}`;
            // 
            this.text = `${trimmedBefore}@${acct} ${after}`;
            // 
            this.vm.$nextTick(() => {
                this.textarea.focus();
                const pos = trimmedBefore.length + (acct.length + 2);
                this.textarea.setSelectionRange(pos, pos);
            });
        }
        else if (type == 'hashtag') {
            const source = this.text;
            const before = source.substr(0, caret);
            const trimmedBefore = before.substring(0, before.lastIndexOf('#'));
            const after = source.substr(caret);
            // 
            this.text = `${trimmedBefore}#${value} ${after}`;
            // 
            this.vm.$nextTick(() => {
                this.textarea.focus();
                const pos = trimmedBefore.length + (value.length + 2);
                this.textarea.setSelectionRange(pos, pos);
            });
        }
        else if (type == 'emoji') {
            const source = this.text;
            const before = source.substr(0, caret);
            const trimmedBefore = before.substring(0, before.lastIndexOf(':'));
            const after = source.substr(caret);
            // 
            this.text = trimmedBefore + value + after;
            // 
            this.vm.$nextTick(() => {
                this.textarea.focus();
                const pos = trimmedBefore.length + value.length;
                this.textarea.setSelectionRange(pos, pos);
            });
        }
    }
}
